<template>
  <div>
    <label><input value="TV" v-model="checked" type="checkbox" />TV</label>
    <label
      ><input value="냉장고" v-model="checked" type="checkbox" />냉장고</label
    >
    <label
      ><input value="에어콘" v-model="checked" type="checkbox" />에어콘</label
    >
    <p>체크한 상품:{{ checked }}</p>

    <select>
      <option disabled value="">음료메뉴를 선택해 주세요</option>
      <option>아메리카노</option>
      <option>카페라떼</option>
      <option>아이스티</option>
    </select>
    <p>선택된 메뉴 :</p>
    <div>
      <label><input type="radio" value="딸기" v-model="picked" />딸기</label>
      <label><input type="radio" value="사과" v-model="picked" />사과</label>
      <label><input type="radio" value="수박" v-model="picked" />수박</label>
      <p>선택한 과일: {{ picked }}</p>
    </div>
    <img src="https://github.com/jmk255/source/blob/main/logo.png?raw=true" />

    <br />

    <input type="text" v-model="idValue" />
    <input type="password" v-model="pwValue" />
    <button type="button" :disabled="idValue == '' || pwValue == ''">
      Click
    </button>
    <br>
    <button :class="{'active': isActive, 'text-red': hasError}">객체로 바인딩</button>
    <div>
      넓이와 높이를 300px로 설정
      배경색을 skyblue로 설정
      폰트색상을 red로 설정
    </div>

    <button v-if="bRender">로그인</button>
    <button v-else>로그아웃</button>
  </div>
</template>

<script>
export default {
  name: "DataBinding",
  data() {
    return {
      checked: [],
      picked: "",
      idValue: "",
      pwValue: "",
      isActive: false, // true면 active클래스가 적용
      hasError: false, // true로 변경되면 text-red클래스가 적용
      bRender:false,
    };
  },
};
</script>

<style scoped>
button {
  width: 150px;
  height: 40px;
  border:0;
  outline:0;
  background-color: tomato;
  color:#fff;
}
button:hover{
  background-color:turquoise;
  color:#333;
}
.active {
  display: none;
}
.text-cyan {
  color: cyan;
}
</style>